@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./tax-list.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ FLY_IN_OUT_ANIMATION ],
})

export class TaxListComponent implements AfterViewInit {
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	@ViewChild(ListComponent) 
	list: ListComponent;
	listHide: boolean = true;
	addHide: boolean = false;
	animateAdd: string = 'out';
	animateList: string = 'in';
	taxForm: FormGroup;
	taxId = 0;  
	tax= new TaxData();
	

	public status:string;
	msg = [];
	
	ngAfterViewInit(){
		if(!this.addHide){
			this.addHide = true;
		}
	}
	
    constructor(private _script: ScriptLoaderService, 	private http: HttpClient,
			    private route:ActivatedRoute) {
	}
	
	DeleteData()
	{
		$(".secondSelect option:checked").remove();
	}
	
	  
    ngOnInit() {
		
		this.listHide = false;
    	this.animateAdd='out';
    	this.animateList='in';
  		this.list.listUrl = sessionStorage.getItem('requestUrl')+"/axelacrm/customer/customer-list";
		this.taxForm = new FormGroup({			
			'taxName' : new FormControl('', [ Validators.required]),
			'taxValue' : new FormControl('', [ Validators.required]),
		});
		
	}
	
	get taxName(){ return this.taxForm.get('taxName')};
	get taxValue(){ return this.taxForm.get('taxValue')};
	
	 
	toggleAnimation(){
		 if(this.animateAdd == 'out' || this.animateList == 'in'){
			this.listHide = true;
			this.addHide = false;
			if(this.taxId == 0){
				this.tax = new TaxData();
				this.status = 'Add';
			}else{
				this.populateData();
				this.status = 'Update';
			}
			this.animateList = 'out';
			this.animateAdd
 = 'in';
			this.taxForm.reset();
		}else{
		
			this.addHide = true;
			this.listHide = false;
			this.taxForm.reset();
			this.list.getData();
			this.animateAdd = 'out';
			this.animateList = 'in';
		}
	  }
	
	
	
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<TaxData>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/customer-data ',
							 			JSON.stringify({ 'taxId' :this.taxId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.tax = data['populatadata'][0];
																		console.log("statusModifiedId=="+this.tax.taxModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
	 
	 
	listClickListener(listParam: any[]){
		if(listParam[0] == 'updateActivityStatus'){
			this.taxId = listParam[1];
			this.toggleAnimation();
			this.taxForm.reset();
		}
	}
	 
	
	 onSubmit() {
		 if(!this.taxForm.invalid){
			   const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.taxId == 0){
			 this.tax['requestType'] = 'add';
			
//			this.customer['tag'] = 'customer';
		}else{
			this.tax['requestType'] = 'update';
			this.tax['taxId'] = this.taxId+'';
		}
//		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/customer',
//									JSON.stringify(this.tax),
//						{ headers, withCredentials: true }).subscribe(data => {
//			 											if(data['msg'].includes('Successfully')){
//														this.msg[0]='success';
//			 											this.msg[1]=data['msg'];
//														}else{
//														this.msg[0]='Error';
//			 											this.msg[1]=data['msg'];
//														}
//			 											this.toast.showtoast(this.msg);
//			 											this.toggleAnimation();
//			 											this.taxForm.reset();
//			 											this.list.getData();
//														},
//														err =>{
//			 												console.log(err['error']['desc']);
//			 												this.msg[0]='Error';
//			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
//		 												});
			 console.log(this.tax);
		 }
	  }
	 
	 onDelete(){
		 this.tax['requestType'] = 'delete';
		 this.tax['taxId'] = this.taxId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/customer',
									JSON.stringify({ requestType : 'delete',
												   'taxId' : this.taxId }),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['msg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['msg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['msg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.taxForm.reset();
			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
		 
	 }
	
}
	
export class TaxData {

    taxId: string;
    taxName: string;
	taxValue:string;
    taxEntryId: string;
    taxEntryDate: string;
    taxModifiedId: string;
    taxModifiedDate: string;
  
}
export class configDetails {
	configCustomerSob:string;
	configCustomerSoe:string;
	configCustomerAddress:string;
}



